import Link from "next/link";
import { Evento, formatarData, formatarPreco } from "../lib/eventos";

export default function EventCard({ evento }: { evento: Evento }) {
  return (
    <Link href={`/eventos/${evento.id}`} className="cardlink">
      <div className="eventcard">
        <div
          className="thumb"
          style={{ backgroundImage: `url(${evento.imagem})` }}
          aria-label={`Imagem do evento ${evento.titulo}`}
        />
        <div className="info">
          <div className="title">{evento.titulo}</div>
          <div className="mutedline">{evento.artista}</div>

          <div className="meta">
            <span className="badge">{formatarData(evento.data)}</span>
            <span className="badge">{evento.horario}</span>
            <span className="badge">{formatarPreco(evento.precoInicial)}</span>
          </div>
        </div>
      </div>

      <style jsx>{`
        .cardlink {
          display: block;
        }

        .eventcard {
          border: 1px solid var(--border);
          background: var(--panel);
          border-radius: 18px;
          overflow: hidden;
          backdrop-filter: blur(10px);
          transition: transform 120ms ease;
        }

        .eventcard:hover {
          transform: translateY(-1px);
        }

        .thumb {
          height: 140px;
          background-size: cover;
          background-position: center;
          border-bottom: 1px solid var(--border);
        }

        .info {
          padding: 14px;
        }

        .title {
          font-weight: 800;
          letter-spacing: 0.2px;
        }

        .mutedline {
          color: var(--muted);
          margin-top: 4px;
          font-size: 13px;
        }

        .meta {
          display: flex;
          gap: 8px;
          flex-wrap: wrap;
          margin-top: 10px;
        }
      `}</style>
    </Link>
  );
}
